.error404-main-container{
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: scroll;
}

.error404-title{
    margin-top: 50px;
    text-align: center;
    align-self: center;
    background-color: #efb54a;
    padding: 10px;
}

.error404-footer{
    width: 80%;
    max-width: 500px;
    text-align: center;
    position: absolute;
    top: 70%;
    background-color: #efb54a;
    padding: 10px;
}

.machine{
    height: 0px;
    width: 160px;
    border-top: 70px solid #735123;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    position:absolute;
    top:30%;
    left:28%;
}
.lower{
    height:30px;
    width:160px;
    background-color: #735123;
    border-radius:5px 5px 0px 0px;
    position: relative;
    left:3px;
    bottom:87px;
}
.bottom{
    height:8px;
    width:170px;
    background-color: #3B240E;
    position: relative;
    top:4px;
    right:5px;
}
.btnn{
    height:15px;
    width:9px;
    background-color: #3B240E ;
    position: relative;
    left:13px;
    top:8px;

}
.light{
    position: relative;
    height:8px;
    width: 8px;
    border-radius: 50%;
    background-color: #E06526;
    bottom:2.2px;
    left:28px;
}
.upper{
    background-color: #3B240E ;
    height:5px;
    width:185px;
    position: relative;
    bottom:70px;
    right:12.5px;
}
.middle{
    height:65px;
    width:60px;
    position: relative;
    background-color: #E06526 ;
    bottom:70px;
    left:50px;

}
.back{
    width:145px;
    background-color: #3B240E;
    height:140px;
    position: relative;
    left:8px;
    bottom:86px;    
}
.cup{
    height: 0px;
    width: 30px;
    border-top: 70px solid #EDE9D7;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    position:relative;
    top:47px;
    left:48px;
    z-index:5;

}
.coff{
    height:30px;
    width: 8px;
    border-radius:0px 0px 15px 15px;
    background-color:#E06526;
    position: relative;
    bottom: 23px;
    left:18px;
    visibility: hidden;
    
}
.machine:hover .coff{
    animation:coffee 6s 4s ;
    -webkit-animation:coffee 6s 4s ;
}
@keyframes coffee{
    0%{
        visibility: visible;
    }
    100%{
        transform: translateY(30px);
    }
}
@-webkit-keyframes coffee{
    0%{
        visibility: visible;
    }
    100%{
        -webkit-transform: translateY(30px);
    }
}

.shadow{
    height: 0px;
    width: 6px;
    border-top: 70px solid #DAD1B7;
    border-left: 0px solid transparent;
    border-right: 10px solid transparent;
    position:relative;
    bottom:70px;
    left:24px;

}
.u{
