.b1{
    width:45px;
    height:16px; 
    background-color:#EDE9D7;
    position: relative;
    bottom:70px;
    left:58px;
    z-index: 5;
}
.b2{
    background-color: #DAD1B7;
    height: 16px;
    width:12px;
    position: relative;
    left:33px;
}
.b3{
    background-color: white;
    height:13px;
    border-radius: 0px 0px 5px 5px;
    width:45px;
    position: relative;
}
.handle{
    height:7px;
    border-right: 70px solid #E06526;
    border-top: 8px solid transparent;
    border-bottom: 8px solid transparent;
    position: relative;
    left:44px;
    bottom:24px;
    
    
}
.machine:hover .handle{
    animation:hndle 6s;
    -webkit-animation:hndle 6s;
}
@keyframes hndle{
    50%{
        transform: translateX(-110px) rotateY(-180deg);

    }
}
@-webkit-keyframes hndle{
    50%{
        -webkit-transform: translateX(-110px) rotateY(-180deg);

    }
}
.h1{
    height:7px;
    width:7px;
    border-radius:3.5px;
    background-color: #E06526;
    position: relative;
    right:3.5px;
}
.h2{
    height:22.5px;
    width:22.5px;
    border-radius:50%;
    background-color: #c9561d;
    position: relative;
    left:58px;
    bottom:14.5px;
