}
.steam{
    background-color: rgba(204, 204, 204,0.2);
    height:10px;
    width:10px;
    border-radius: 50%;
    position: relative;
    left:22px;
    
}
.machine:hover .steam{
    animation:stm 4s 4s;
    -webkit-animation:stm 4s 4s;
}
@keyframes stm{
    100%{
        bottom:45px;
    }
}
@-webkit-keyframes stm{
    100%{
        bottom:45px;
    }
}
.s1{
    background-color: rgba(230, 230, 230,0.3);
    height:6px;
    width:6px;
    position: relative;
    bottom:2px;
    border-radius: 50%;
}
.drop{
    background-color: #E06526;
    height:10px;
    width:10px;
    border-radius:50%;
    position: relative;
    bottom:62.5px;
    left:16.7px;
    visibility: hidden;
    
}
.machine:hover .drop{
    animation: drop 2s 6.1s;
    -webkit-animation: drop 2s 6.1s;
}
@keyframes drop{
    100%{
        visibility: visible;
        transform: translateY(20px);
    }
}
@-webkit-keyframes drop{
    100%{
        visibility: visible;
        -webkit-transform: translateY(20px);
    }
}